import "./index.scss";
import { List } from "antd-mobile";
const Item = List.Item;

export class ViewDemo extends Component {
  
  render() {
    const { word, pic } = this.props;
    return (
      <div>
        <List renderHeader={() => ""} className="my-list">
          <Item  thumb={pic}>
            {word.creator}
          </Item>
        </List>
        <List>
          <div
            style={{
              display: "block",
              position: "relative",
              color: "#333",
              boxShadow: "1px 1px 1px 1px rgba(192, 192, 192, 0.8)",
              background: "#fff",
              textAlign: "center",
              width: "80%",
              marginLeft: "0.8rem",
              paddingBottom: "0.2rem"
            }}
          >
            <a
              href={`#/detail/${word.id}`}
              style={{
                display: "block",
                position: "relative",
                color: "#333"
              }}
            >
              <img
                src={word.img}
                alt=""
                style={{
                  width: "100%",
                  verticalAlign: "top",
                  height: "3rem",
                  marginBottom: "1rem"
                }}
                onLoad={() => {
                  window.dispatchEvent(new Event("resize"));
                }}
              />
              <div
                className="content"
                style={{ position: "relative", padding: "0.4rem" }}
              >
                <p
                  className="hitokoto"
                  style={{
                    height: "3rem",
                    marginBottom: "0.2rem"
                  }}
                >
                  {word.hitokoto}
                </p>
                <p
                  className="fromTitle"
                  style={{
                    textAlign: "center",
                    overflow: "hidden",
                    width: "100%",
                    whiteSpace: "nowrap",
                    textOverflow: "ellipsis"
                  }}
                >
                  -《{word.from}》-
                </p>
              </div>
              <p style={{ marginLeft: "0.1rem", textAlign: "left" }}>
                #{word.type.text}
              </p>
            </a>
          </div>
        </List>
      </div>
    );
  }
}
