import "./index.scss";
import { Head } from "~/components/head";
import { Tabs, WhiteSpace } from "antd-mobile";
import { connect } from "react-redux";
import { getTypeList, getWord } from "../../actions";
import { ViewDemo } from "../../components/demo";
const tabs = [{ title: "热门" }, { title: "发现" }];

@connect(state => {
  return {
    typeList: state.getIn(["data", "typeList"]),
    banner: state.getIn(["data", "banner"])
  };
})
export default class Discuss extends Component {
  constructor() {
    super();
    this.state = {
      pic: require("@/assets/images/touxiang.jpg")
    };
  }
  componentDidMount() {
    this.props.dispatch(
      getTypeList({
        url: "/react/getTypeList"
      })
    );
    if (this.props.banner.size == "") {
      this.props.dispatch(
        getWord({
          url: "/react/getWord"
        })
      );
    }
  }
  render() {
    const { typeList, banner } = this.props;
    const { pic } = this.state;
    console.log(typeList);
    console.log(banner);
    return (
      <div style={{ marginBottom: "0.9rem" }}>
        <Head title="讨论" rightShow={true}></Head>
        <Tabs
          tabs={tabs}
          initialPage={0}
          tabBarInactiveTextColor="#ccc"
          tabBarActiveTextColor="#000"
          tabBarUnderlineStyle={{ border: "none" }}
        >
          <div>
            {banner.map((word, i) => {
              return <ViewDemo key={i} word={word} pic={pic}></ViewDemo>;
            })}
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}
          >
            <Tabs
              tabs={typeList}
              initialPage={1}
              tabBarInactiveTextColor="#ccc"
              tabBarActiveTextColor="#000"
              tabBarUnderlineStyle={{ border: "none" }}
              // onChange={(tab, index) => {
              //   console.log("onChange", index, tab);
              // }}
              // onTabClick={(tab, index) => {
              //   console.log("onTabClick", index, tab);
              // }}
            >
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center"
                }}
              >
                {typeList.map((item, i) => {
                  if (i == 0) {
                    return (
                      <div key={i}>
                        {banner.map((word, i) => {
                          if (word.type.text == item.title) {
                            return (
                              <ViewDemo
                                key={i}
                                word={word}
                                pic={pic}
                              ></ViewDemo>
                            );
                          }
                        })}
                      </div>
                    );
                  }
                })}
              </div>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center"
                }}
              >
                {typeList.map((item, i) => {
                  if (i == 1) {
                    return (
                      <div key={i}>
                        {banner.map((word, i) => {
                          if (word.type.text == item.title) {
                            return (
                              <ViewDemo
                                key={i}
                                word={word}
                                pic={pic}
                              ></ViewDemo>
                            );
                          }
                        })}
                      </div>
                    );
                  }
                })}
              </div>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center"
                }}
              >
                {typeList.map((item, i) => {
                  if (i == 2) {
                    return (
                      <div key={i}>
                        {banner.map((word, i) => {
                          if (word.type.text == item.title) {
                            return (
                              <ViewDemo
                                key={i}
                                word={word}
                                pic={pic}
                              ></ViewDemo>
                            );
                          }
                        })}
                      </div>
                    );
                  }
                })}
              </div>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center"
                }}
              >
                {typeList.map((item, i) => {
                  if (i == 3) {
                    return (
                      <div key={i}>
                        {banner.map((word, i) => {
                          if (word.type.text == item.title) {
                            return (
                              <ViewDemo
                                key={i}
                                word={word}
                                pic={pic}
                              ></ViewDemo>
                            );
                          }
                        })}
                      </div>
                    );
                  }
                })}
              </div>
            </Tabs>
            <WhiteSpace />
          </div>
        </Tabs>
        <WhiteSpace />
      </div>
    );
  }
}
